html {
  height: 100vh;
  margin: 0;
  background: #673ab7;
  align-items: center;
  background: #673ab7; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to top,
    #512da8,
    #673ab7
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to top,
    #512da8,
    #673ab7
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
body {
  margin: 0;
  height: 100vh;
}
